.sleepover_container {
  display: grid;
  grid-template-columns: repeat(2, auto);
  grid-template-rows: repeat(2, auto);
  grid-template-areas:
    "sleepover_title sleepover_title"
    "sleepover_main_text sleepover_main_text"
    "sleepover_contact_person sleepover_contact_number"
    "sleepover_directions_plan sleepover_directions_plan";
}

.sleepover_container > div {
  margin-left: 10px;
  margin-right: 10px;
}

@supports (display: grid) {
  @media screen and (max-width: 1000px) {
    .sleepover_title_photo {
      height: 10vw;
      user-select: none;
      pointer-events: none;
    }
    .sleepover_button_style {
      width: 30vw;
      height: 10vw;
      border-radius: 5vw;
      font-size: 15px;
      background-color: rgba(0, 0, 0, 1);
      border-color: rgba(0, 0, 0, 1);
      color: rgba(255, 255, 255, 1);
      outline: none;
      user-select: none;
    }
  }
  @media screen and (min-width: 1000px) {
    .sleepover_title_photo {
      height: 6vw;
      user-select: none;
      pointer-events: none;
    }
    .sleepover_button_style {
      width: 150px;
      height: 50px;
      border-radius: 25px;
      font-size: 25px;
      background-color: rgba(0, 0, 0, 1);
      border-color: rgba(0, 0, 0, 1);
      color: rgba(255, 255, 255, 1);
      outline: none;
      user-select: none;
    }
  }
}

.sleepover_title {
  grid-area: sleepover_title;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin-bottom: 15px;
}

.sleepover_main_text {
  grid-area: sleepover_main_text;
  user-select: none;
  pointer-events: none;
  margin-bottom: 10px;
}

.sleepover_contact_person {
  grid-area: sleepover_contact_person;
  user-select: none;
  pointer-events: none;
}
.sleepover_contact_number {
  grid-area: sleepover_contact_number;
}


.sleepover_directions_plan {
  margin-top: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  grid-area: sleepover_directions_plan;
}

.sleepover_button_animated {
  -webkit-animation-duration: 5s;
  animation-duration: 5s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
  -webkit-animation-timing-function: linear;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  -webkit-animation-iteration-count: infinite;
}
@-webkit-keyframes itinerary_button_bounce {
  0% {
    -webkit-transform: translateY(0);
  }
  2% {
    -webkit-transform: translateY(-5px);
  }
  4% {
    -webkit-transform: translateY(0);
  }
  100% {
    -webkit-transform: translateY(0);
  }
}
@keyframes itinerary_button_bounce {
  0% {
    -webkit-transform: translateY(0);
  }
  2% {
    -webkit-transform: translateY(-5px);
  }
  4% {
    -webkit-transform: translateY(0);
  }
  100% {
    -webkit-transform: translateY(0);
  }
}
.sleepover_button_bounce {
  -webkit-animation-name: itinerary_button_bounce;
  animation-name: itinerary_button_bounce;
}
